import React from 'react';
import MarkDown from '@/views/Wirkung/components/markdown/markdown-edit' 
const content=`
### 为什么要使用super()?
  1. 使用super(),这样在constructor构造器里面就可以使用this
  2. super(props),这样就可以使用this.props
\`\`\` js
  class Example extends React.Component{
     constructor(props){
        super(props) //这样定义就可以使用this,this.props
     }
  }
\`\`\`
`
export default class ReactKnowledgeDemo4 extends React.Component {
    render() {
        const markConfig={
          markConfig:{  
            md:false,
            menu:false,
            toolbar_show:false,
            height:`100%`           
          },
          markContent: content
        }
        return (
            <div>              
                <div style={{height:'467px'}}>
                   <h2 className="h2_common">React-constructor与super</h2>
                   <MarkDown {...markConfig}/>
                </div>
            </div>
        )
    }
}