class Child extends React.Component{
  getFVal=()=>{
    console.log('获取父传过来值content：',this.props.content)
    this.props.sendFun('我是子组件，我传给父一个true')
  }
  render(){
    return  (
      <div>
          <h2>我是子组件</h2>
          <p>{this.props.content}</p>
          <button onClick={this.getFVal}>子获取父的值并传值给父</button>
      </div>
    )
  }
}
export default class ReactKnowledgeDemo3 extends React.Component {
    sendFun=(sonVal)=>{
        console.log('子组件传来值：',sonVal)
    }
    render() {
        const markConfig={
          markConfig:{  
            md:false,
            menu:false,
            toolbar_show:false,
            height:`100%`         
          },
          markContent: content
        }
        return (
            <div>              
                <div style={{height:'467px'}}>
                   <h2 className="h2_common">React-父子组件通信-props</h2>
                   <MarkDown {...markConfig}/>
                </div>
                <Child content='父组件传给子组件值：false' sendFun={this.sendFun}></Child>
            </div>
        )
    }
}
