export default class ReactKnowledgeDemo2 extends React.Component {
    constructor(props){
       super(props)
       this.state={
          count:1,
          isActive:true
       }
    }
    clickChangeState1=()=>{
      this.setState({
        isActive:false
      },()=>{
        console.log('isActive',this.state.isActive)
      })
    }
    clickChangeState2=async ()=>{
      await this.setState({
        isActive:false
      })
      console.log('isActive',this.state.isActive)
    }
    render() {
        const markConfig={
          markConfig:{  
            md:false,
            menu:false,
            toolbar_show:false,
            height:`100%`       
          },
          markContent: content
        }
        return (
            <div>
                <div style={{height:'467px'}}>
                  <h2 className="h2_common">React-state用法</h2>
                  <MarkDown {...markConfig}/>
                </div>
                {/* <button onClick={this.clickChangeState1}>2.1 测试</button> */}
                {/* <button onClick={this.clickChangeState2}>2.1 测试</button> */}
            </div>
        )
    }
}