// import { AnyAction, combineReducers } from "redux"
import { combineReducers } from "redux"
import * as ActionTypes from './action-types' //引入用户类型常量

// 初始化共享数据值
let initialState = {
  addressList: [],   // 地址列表
  addressName: '',  // 选中的地址
  temMessage: '', //临时姓名
  hasAddressList: [], // 已有的地址
  operate: 'edit',
  geohash: [],
  banners:[],
  userInfo:JSON.parse(localStorage.getItem('USER'))||{},//用户信息
}

function combineBanners( banners= initialState.banners , action={} ){
  switch (action.type) {
    case ActionTypes.SET_BANNERS:
      return action.banners;
    default:
      return banners;
  }
}
function combineUserInfo (userInfo= initialState.userInfo , action={}){
  switch (action.type) {
    case ActionTypes.SET_USERINFO:
      return action.userInfo;
    default:
      return userInfo;
  }
}
const reducer  = combineReducers({//成功引入一个共享数据和action
  banners:combineBanners, //banners 是 mapStateToProps函数store提供的对象属性
  userInfo:combineUserInfo

})
export default reducer