//2. 祖父组件
import React from 'react'
export default class ReactKnowledgeDemo5 extends React.Component {
  state={
    toMyChild:'hahahaha'
  }
  render() {
      return (
          <div>             
              {/* 祖父组件通过 Provider提供数据 */}
              <demo5Context.Provider value={this.state}> 
                 <Father></Father>
              </demo5Context.Provider>
          </div>
      )
  }
}
